/* styling for each individual post on explore page */
.posts-index-one {
  max-width: 800px;
  padding: .5rem;
  background: #f5efe7;
  margin: 10px 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

/* styling for entire grid of posts on explore page */
.posts-index-all {
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: -7vw;
  margin-right: -7vw;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}



